import React from 'react'
import $ from 'jquery'
import Cards from './Cards'

class TopRated extends React.Component{
    constructor(props){
        super(props);
        this.state = {}
        this.topRated()
    }
    topRated(){
        const urlString = "https://api.themoviedb.org/3/movie/top_rated?api_key=5f793d033ea33558e13b3664b3eadca9&language=en-US&page=1"
        $.ajax({
          url: urlString,
          success: (searchResult) => {
            const result = searchResult.results
            var movieRows = []
            result.forEach((movie) => {
              movie.poster_src =  movie.poster_path
             const movieRow = <Cards key={movie.id} movie={movie}/>
             movieRows.push(movieRow)
            })
            this.setState({rows:movieRows})
              },
              error: (xhr, status, err) => {
                console.error("failed fetch")
              }
          })
        }
    
    
      render(){
      return (
         
         <div className="marginUP" id="pop">
                    <h2 className="pl-5">Top Rated Movies</h2>
                    <div className="container-fluid">
                        <div className="row">
             <div className="col w-100 d-flex flex-nowrap film" id="popular">{this.state.rows}</div>
                        </div>
                    </div>
            </div>
      );
    }
    }
export default TopRated