import React from 'react'
class Cards extends React.Component{

  
    viewMovie(){
        // console.log('bom')
        // console.log(this.props.movie.title)
        const url = "https://www.themoviedb.org/movie/" + this.props.movie.id
        window.location.href = url
      }
        render(){
            return  <div className=" col-xl-2 6 col-mb-3 mt-3 " style={{width: "12rem", padding:"4px"}}>
              <div className="card" style={{backgroundColor:"teal", height:"400px"}}>
            {
             this.props.movie.poster_src == null ? <img src={`https://muwaija.com/static/img/no_image.png`}  alt="image not avaible" style={{height:"278px"}}/> : <img src={`https://image.tmdb.org/t/p/w500/${this.props.movie.poster_src}`} alt="movie-poster" style={{height:"278px"}}/>
            }
         <div className="card-body">
           <h5 className="card-title" style={{fontSize:"13px" ,fontWeight:"bold"}}>{`${this.props.movie.title}`}</h5>
               <p className="card-text" style={{fontSize:"13px" }}>
                  {`Relase date : ${this.props.movie.release_date}`}
              </p>
           <p style={{position:"absolute", top:"0", left:"0",  borderBottomRightRadius:"6px",color:"white",backgroundColor:"black"}}>{`${this.props.movie.vote_average} `}
           <svg width="1em" padding="2px" height="1em" viewBox="0 0 16 16" className="bi bi-star-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
               <path d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.283.95l-3.523 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z"/>
                   </svg>
           </p>
             
            </div>
         </div>
         </div>
        }
}

export default Cards