import React from 'react';
import './App.css';
import $ from 'jquery'
// import Nav from './component/nav'
import UpcomingMovie from './component/UpcomingMovie';
import TopRated from './component/TopRated'
import TrendingMovie from './component/TrendingMovie'
import Carousel from './component/carousel'
import Footer from './component/Footer'
import Cards from './component/Cards'


class App extends React.Component {
  constructor(props){
    super();
    this.state={}
    this.performSeach(this.performSeach)
  }
  performSeach(searchTerm){
    const urlString ="https://api.themoviedb.org/3/search/movie?api_key=5f793d033ea33558e13b3664b3eadca9&language=en-US&query=" + searchTerm
    
    $.ajax({
      url: urlString,
      success :(searchResult) =>{
        var result = searchResult.results 
        
        
        var movieRows =[]

        result.forEach((movie) =>{
          movie.poster_src = movie.poster_path

          const movieRow = <Cards key={movie.id} movie={movie}/>
          movieRows.push(movieRow)
        })
        this.setState({rows: movieRows})
      },
      error: (xhr, status, err) => {
        console.error("failed fetch")
      }
    })
  }

  searchChangeHandler(event) {
      const boundObject = this
      const searchTerm = event.target.value
      boundObject.performSeach(searchTerm)
  }
  

  render(){
  return (
    <div className="App">
   
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <a className="navbar-brand" href="#">MovieGeeK</a>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
      
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item active">
            
            </li>
          </ul>


          <form className="form-inline my-3 my-lg-0">
            <input className="form-control mr-sm-2" onChange={this.searchChangeHandler.bind(this)} type="search" placeholder="Search" aria-label="Search"/>
          </form>
        </div>
       
      </nav>
     
    
          <div className="row">
             <div className="col w-100 d-flex flex-nowrap film" >{this.state.rows}</div>
              </div>      
         <Carousel/> 
         <UpcomingMovie/>
         <TopRated/>
         <TrendingMovie/>
         <Footer />
           </div>  
           
  );
}
}

export default App;